/* Gridsystem */
 
.grid-layout {
  display: grid;
  grid-template-areas:
    'one one'
    'two two'
    'three four ';
    
  grid-gap: 1rem;
 
}

.grid-one {
  grid-area: one;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  grid-gap: 1rem;
}

/* Back To Profiles*/
.profile-button{
  grid-area: one ;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-gap: 1rem;
}

.grid-one img {
