/* Landing Page */

.landing {
  position: relative;
  height: 100vh;
}

.landing-inner {
  color: #fff;
  height: 100%;
  width: 80%;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
/* Utilities */

.container {
  max-width: 1100px;
  margin: auto;
  overflow: hidden;

  padding: 2rem 2rem;
  margin-top: 6rem;
  margin-bottom: 3rem;
}

.flexbox { 
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 3rem;
}


.border { 
  border-radius: 1rem;
  border: 5px;
  border-color: rgb(252, 247, 247);
}

.flex-nowrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 3rem;
}

.grey-icon {
  color:#e6dfdf;
}


.display-inline {
  display:inline-block; 
}



.center { 
  justify-content: center;
  text-align: center;
}

/* Text Styles*/

.line {
  height: 1px;
  background: #ccc;
  margin: 1.5rem 0;
}

.x-large {
  font-size: 4rem;
