}



/* Forms */
.form .form-group {
  margin: 1.2rem 10rem;
   padding: 0.3rem !important;
}

.form p, 
.grey {
  font-size: 0.8rem;
  margin-top:0.5rem;
}

.form .form-text {
  display: block;
  margin-top: 0.3rem;
  color: #888;
}

.form input[type='text'],
.form input[type='password'],
.form input[type='date'],
.form input[type='number'],
.form select,
.form textarea {
  display: block;
  width: 100%;
  padding: 0.4rem;
  padding-left:0.8rem;
  font-size: 1.1rem;
  border:  1px solid #ccc;
  transform: capitalize;
  border-radius:0.5rem;
}

.form input[type='submit'],
button {
  font: inherit;
}

.form input:hover[type='text'],
.form input:hover[type='password'],
.form input:hover[type='date'],
.form input:hover[type='number'],
.form input:hover[type='select'],
.form select:hover
 {
  color:var(--dark-color);
  font-weight:bold;
}

.form textarea:hover{ 
  color:var(--dark-color);
}



